<template>
  <div id="improve-info">
    <div class="main">
      <div class="improve-main">
        <h3>完善信息</h3>
        <div class="info-avatar">
          <span v-if="avatar == ''">头像</span>
          <img :src="avatar" v-else class="avatar">
          <input type="file" accept="image/*" id="image_input" @change="uploadImg">
        </div>


        <input type="text" v-model="name" class="bb-ccc" placeholder="请输入昵称">
        <div class="btn">下一步</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        name: '',
        avatar: '',
      }
    },
    watch: {},
    methods: {
      uploadImg(){
        var img = document.querySelector('#image_input');
        var file = img.files[0];
        var reader = new FileReader();
        reader.onload = () =>{
          // reader.result;
          this.avatar = reader.result
//          console.log(reader.result);
        }
        reader.readAsDataURL(file);

      },
    }
  }
</script>

<style lang="scss">
  @import "../../sass/baseSass.scss";
  #improve-info{
    height: 100%;
    background: #fff;
    width: 100%;

    .main{
      height: 100%;
      width: 100%;
      background: url("../../assets/home/logo.png") no-repeat center top;
      background-size: 100% auto;
    }
    .improve-main{
      width: 8rem;
      height: 8.43rem;
      padding: 0.44rem;
      position: absolute;
      bottom: 2rem;
      left: 1rem;
      box-shadow: 0rem -0.07rem 0.13rem 0.01rem rgba(67, 67, 67, 0.06), 0rem 0.07rem 0.13rem 0rem rgba(0, 0, 0, 0.13);
      border-radius: 0.11rem;
      h3{
        text-align: center;
        font-size: 0.48rem;
        color: #de7fef;
        margin-bottom: 0.25rem;
      }
      input{
        width: 100%;
        height: 1.2rem;
        border: none;
        font-size: 0.37rem;
        color: #333;
        background: #fff;
        position: relative;
      }
      .bb-ccc{
        border-bottom: 1px #d8d8d8 solid;
      }
    }
    .info-avatar{
      width: 2rem;
      height: 2rem;
      background-color: #cccccc;
      border: solid 0.01rem #cccccc;
      margin: 0.53rem auto;
      border-radius: 1rem;
      line-height: 2rem;
      text-align: center;
      color: #fff;
      position: relative;
      overflow: hidden;
      font-size: 0.45rem;

      input#image_input{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        filter:alpha(opacity=0);
        -moz-opacity:0;
        -khtml-opacity: 0;
        opacity: 0;
      }

      .avatar{
        width: 100%;
        height: 100%;
        border-radius: 1rem;
      }
    }
    .btn{
      width: 6.93rem;
      height: 1.07rem;
      background-image: linear-gradient(135deg,
      #fe9bba 0%,
      #de7fef 100%);
      border-radius: 0.53rem;
      margin: 0 auto;
      line-height: 1.07rem;
      color: #fff;
      font-size: 0.43rem;
      text-align: center;
      margin-top: 0.53rem;
    }

  }

</style>



